.k-calendar-table .k-calendar-thead{
  display: contents !important;
}

.label-left .k-floating-label-container {
  flex-direction: row;
  align-items: center;
  padding-top: 0px;
  margin-top: -2px;
}

.label-left .k-label {
  position: static !important;
  margin-bottom: 0px !important;
  display: block;
  order: -1;
  margin-right: 10px;
}

.k-calendar .k-calendar-view .k-today {
  color: #0c212b;
}

.k-timeselector .k-time-part .k-time-header button{
display: none;
}

.k-calendar .k-calendar-td.k-selected .k-link {
  border-color: #008662 !important;
  color: white;
  background-color: #008662 !important;
}
.k-button-solid-primary {
  border-color: #008662 !important;
  color: white;
  background-color: #008662 !important;
}
.k-calendar .k-nav-today, .k-calendar .k-calendar-nav-today {
  color: #0c212b !important;
}
.k-time-header .k-time-now, .k-time-selector-header .k-time-now {
  color:  #0c212b !important;
}
.k-item:hover{
color: #0c212b !important;
}
.k-calendar .k-range-start, .k-calendar .k-range-end, .k-calendar .k-range-mid { 
  background-color: #90ee90  !important;
}
.k-button-solid-base.k-selected {
  border-color: #008662 !important;
  color: white !important;
  background-color: #008662 !important;
}

.k-input-inner {
    color: #000;
    font-size: 12px;
    font-weight: 100;
    height: 31px;
}

.k-input-button {
  border: none;
}

.k-input-solid.k-invalid{
border-color: rgba(0, 0, 0, 0.08);
}

.k-animation-container{
  z-index: 100000 !important;
}

.k-input-inner:disabled {
  background-color: #B1B1B1;
  border-color: #CED4DA;
}

.k-input-inner:disabled,
.k-input-inner:disabled::placeholder {
  color: #FAFAFA;
}

.k-text-error {
  color: #0c212b;
}

.k-scheduler-row {
  overflow: hidden !important;
}