.successDiv {
    margin-top: 20px;
    border: 1px solid #e2e8f0;
  }

  .successBody {
    height: calc(100vh - 300px);
    overflow: auto;
    display: block;
  }
  
  .trBorder {
    border: 1px solid #e2e8f0;
  }
  
  .formCheck:checked {
    background-color: #008662 !important;
    border-color: #008662 !important;
  }
  
  .saveButton {
    height: 36px;
    font-size: 12px;
    line-height: 12px;
    font-weight: 100;
    padding: 11px 24px;
    color: #ffffff;
    background-color: #008662;
    border: none;
    border-radius: 4px;
  }
  
  .saveButton:disabled {
    background-color: #b1b1b1 !important;
    cursor: not-allowed;
  }

.tableHeader{
    text-indent: 20px;
    padding: 5px 0;
    color: #656565;
    font-size: 13px;
    font-weight: bolder;
}

.tableHead, .tableBody .tableRow {
  display: table;
  width: 100%;
  table-layout: fixed;/* even columns width , fix width of table too*/
}

